// src/WelcomeScreen.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHelmetSafety } from '@fortawesome/free-solid-svg-icons'; // Ikon helm

const WelcomeScreen = () => {
  const navigate = useNavigate();

  const handleStart = () => {
    navigate('/summary'); // Mengarahkan ke halaman Summary
  };

  return (
    <div style={{
      textAlign: 'center',
      margin: 0,
      padding: 0,
      backgroundColor: 'white', // Background putih
      minHeight: '100vh',
      color: 'black',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      height: '100vh',
    }}>
      <FontAwesomeIcon icon={faHelmetSafety} size="6x" color="black" /> {/* Ikon helm hitam */}
      <h1>Selamat Datang di Sistem Monitoring Pelanggaran Helm</h1>
      <button 
        onClick={handleStart} 
        style={{
          padding: '15px 30px',
          fontSize: '18px',
          cursor: 'pointer',
          marginTop: '20px',
          backgroundColor: 'white', // Latar belakang putih
          color: 'black', // Teks hitam
          border: '2px solid black', // Border hitam
          borderRadius: '5px',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          transition: 'background-color 0.3s, transform 0.3s',
        }}
        onMouseEnter={(e) => {
          e.target.style.backgroundColor = '#f0f0f0'; // Warna saat hover
          e.target.style.transform = 'scale(1.05)'; // Efek zoom saat hover
        }}
        onMouseLeave={(e) => {
          e.target.style.backgroundColor = 'white'; // Kembali ke warna semula
          e.target.style.transform = 'scale(1)'; // Kembali ke ukuran semula
        }}
      >
        Mulai
      </button>
    </div>
  );
};

export default WelcomeScreen;
