// src/ListPelanggaran.js
import React, { useState, useEffect } from 'react';
import { storage, ref, listAll, getDownloadURL } from './firebaseConfig';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faListAlt } from '@fortawesome/free-solid-svg-icons'; // Ikon daftar

const ListPelanggaran = () => {
  const [pelanggaran, setPelanggaran] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchImages = async () => {
      try {
        const listRef = ref(storage, 'images/'); // Folder di Firebase Storage tempat gambar disimpan
        const res = await listAll(listRef);
        const urls = await Promise.all(res.items.map(async (item) => {
          const url = await getDownloadURL(item);
          // Mengambil timestamp dari nama file
          const timestamp = item.name.split('_')[2].split('.')[0]; // Misal nama file: detected_image_1725846858.jpg
          return { url, timestamp };
        }));
        setPelanggaran(urls);
      } catch (error) {
        console.error("Error fetching images: ", error);
      } finally {
        setLoading(false);
      }
    };

    fetchImages();
  }, []);

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <FontAwesomeIcon icon={faListAlt} size="6x" color="black" />
      <h1>Daftar Pelanggaran</h1>
      {loading ? (
        <div>Loading...</div>
      ) : (
        <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>
          {pelanggaran.length > 0 ? (
            pelanggaran.map((item, index) => (
              <div key={index} style={{ margin: '10px' }}>
                <img src={item.url} alt={`Pelanggaran ${index}`} style={{ width: '300px', border: '2px solid black' }} />
                <div>
                  <p>Tanggal dan Waktu: {new Date(item.timestamp * 1000).toLocaleString()}</p>
                </div>
              </div>
            ))
          ) : (
            <div>Tidak ada gambar pelanggaran yang ditemukan.</div>
          )}
        </div>
      )}
    </div>
  );
};

export default ListPelanggaran;
