import React, { useState, useEffect } from 'react';
import { getStorage, ref as storageRef, listAll, getDownloadURL } from 'firebase/storage';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHelmetSafety } from '@fortawesome/free-solid-svg-icons'; // Ikon helm

const Summary = () => {
  const [pelanggar, setPelanggar] = useState(0);
  const [weeklySummary, setWeeklySummary] = useState({});
  const [lastImageUrl, setLastImageUrl] = useState('');
  const [loading, setLoading] = useState(true);
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);

  useEffect(() => {
    const fetchImages = async () => {
      const storage = getStorage();
      const listRef = storageRef(storage, 'images/');

      try {
        const res = await listAll(listRef);
        let totalPelanggar = 0;
        let weeklyCounts = {};
        let lastImage = '';

        for (const item of res.items) {
          const timestamp = item.name.split('_')[2].split('.')[0]; // Mengambil timestamp dari nama file
          const dateString = new Date(parseInt(timestamp) * 1000).toISOString().split('T')[0]; // Format YYYY-MM-DD
          const url = await getDownloadURL(item);

          // Hitung total pelanggar untuk tanggal yang dipilih
          if (dateString === selectedDate) {
            totalPelanggar += 1;
            lastImage = url; // Ambil URL gambar pelanggar terakhir
          }

          // Hitung pelanggaran per hari
          if (weeklyCounts[dateString]) {
            weeklyCounts[dateString] += 1;
          } else {
            weeklyCounts[dateString] = 1;
          }
        }

        // Set state untuk total pelanggar dan ringkasan mingguan
        setPelanggar(totalPelanggar);
        setWeeklySummary(weeklyCounts);
        setLastImageUrl(lastImage);
      } catch (error) {
        console.error("Error mengambil data dari Firebase Storage: ", error);
      } finally {
        setLoading(false);
      }
    };

    fetchImages();
  }, [selectedDate]); // Menambahkan selectedDate sebagai dependency

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <FontAwesomeIcon icon={faHelmetSafety} size="6x" color="black" />
      <h1>Ringkasan Pelanggaran</h1>
      <div style={{ margin: '20px 0' }}>
        <input 
          type="date" 
          value={selectedDate} 
          onChange={(e) => setSelectedDate(e.target.value)} 
          style={{ padding: '10px', fontSize: '16px' }} 
        />
      </div>
      <div>
        <h2>Total Pelanggar pada {selectedDate}:</h2>
        <div style={{ fontSize: '48px', fontWeight: 'bold' }}>{pelanggar}</div>
      </div>
      {lastImageUrl && (
        <div>
          <p>Gambar pelanggar terakhir:</p>
          <img src={lastImageUrl} alt="Pelanggar Terakhir" style={{ width: '300px', marginTop: '20px' }} />
        </div>
      )}
      <div style={{ marginTop: '30px' }}>
        <Link to="/list-pelanggaran">
          <button 
            style={{
              padding: '10px 20px',
              fontSize: '16px',
              cursor: 'pointer',
              backgroundColor: '#fff',
              color: 'black',
              border: '2px solid black',
              borderRadius: '5px',
              marginRight: '10px'
            }}>
            Lihat Daftar Pelanggaran
          </button>
        </Link>
        <Link to="/grafik-pelanggaran">
          <button 
            style={{
              padding: '10px 20px',
              fontSize: '16px',
              cursor: 'pointer',
              backgroundColor: '#fff',
              color: 'black',
              border: '2px solid black',
              borderRadius: '5px'
            }}>
            Lihat Grafik Pelanggaran
          </button>
        </Link>
      </div>
    </div>
  );
};

export default Summary;
