import React, { useState, useEffect } from 'react';
import { getStorage, ref as storageRef, listAll, getMetadata } from 'firebase/storage';
import { Bar } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHelmetSafety } from '@fortawesome/free-solid-svg-icons';

Chart.register(...registerables);

const GrafikPelanggaran = () => {
  const [weeklyCounts, setWeeklyCounts] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const storage = getStorage();
        const listRef = storageRef(storage, 'images/');
        const res = await listAll(listRef);

        const today = new Date();
        const weekCounts = Array(7).fill(0);
        const weekDates = [];

        // Generate dates for the past week
        for (let i = 0; i < 7; i++) {
          const date = new Date(today);
          date.setDate(today.getDate() - i);
          const dateString = date.toISOString().split('T')[0];
          weekDates.push(dateString);
        }

        // Fetch metadata for each image
        for (const itemRef of res.items) {
          const metadata = await getMetadata(itemRef);
          const uploadDate = new Date(metadata.timeCreated).toISOString().split('T')[0];

          // Update count for the corresponding date
          const index = weekDates.indexOf(uploadDate);
          if (index !== -1) {
            weekCounts[index] += 1;
          }
        }

        setWeeklyCounts(weekCounts.reverse());
      } catch (error) {
        console.error("Error fetching images or metadata: ", error);
      }
    };

    fetchData();
  }, []);

  const data = {
    labels: ['Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu', 'Minggu'],
    datasets: [
      {
        label: 'Jumlah Pelanggaran',
        data: weeklyCounts,
        backgroundColor: 'rgba(0, 0, 0, 0.6)',
      },
    ],
  };

  const options = {
    scales: {
      y: {
        beginAtZero: true,
        ticks: {
          display: false, // Menyembunyikan label sumbu Y
        },
      },
    },
    plugins: {
      tooltip: {
        callbacks: {
          label: function (context) {
            return 'Jumlah Pelanggar: ' + context.raw; // Menampilkan jumlah pelanggar di tooltip
          },
        },
      },
    },
  };

  return (
    <div style={{ textAlign: 'center', marginTop: '20px', maxWidth: '900px', margin: 'auto' }}>
      <FontAwesomeIcon icon={faHelmetSafety} size="6x" color="black" />
      <h1>Grafik Pelanggaran Mingguan</h1>
      <div style={{ height: '900px', width: '100%' }}>
        <Bar data={data} options={options} />
      </div>
    </div>
  );
};

export default GrafikPelanggaran;
